html {
    overflow: -moz-hidden-unscrollable;
    height: 100%;
}
body::-webkit-scrollbar {
    max-width: 100%;
	display: none;
	overflow-x:hidden;
}
#pic_tag {
    float: right;
    margin: 1.2rem 0.5rem 0 0;
}
.el-message--success {
  z-index: 3000 !important;
}
.el-message--error {
  z-index: 3000 !important;
}
@media screen and (min-width: 360px){
#control {
        float: right;
        display:inline-block;
}
.item {
        float: right;
        margin: 1rem;
}
.zan {
        margin: 1rem 1rem 0 0;
        font-size: 40px;
        float:right;
}
.picBoard {
}
}
@media screen and (max-width: 360px) {
#out {
        width: 100%;    
        height: 70px;
        display: block;
}
#control {
        float: none;
}
.item {
        margin: 1rem 0.5rem 1rem 0.5rem;
}
.zan {
        margin: 0.6rem 1rem 0 0;
        height: 30px;
        font-size: 40px;
        float:right;
}
.picBoard {
        -o-object-fit: cover;
           object-fit: cover;
}
}
.zan:active {
    -webkit-transform: scale(1.5);
            transform: scale(1.5);
}




@media screen and (min-width: 320px) and (max-width: 1156px){
#content {
        margin: 0 1rem;
}
.article_card {
        margin:0.3rem 0rem;
        display: inline-block;
        width: 100%;
        font-size: 12px;
}
}
@media screen and (min-width: 1156px) {
#content {
        margin: 0 3rem;
}
.article_card {
        margin: 0.3rem 1rem;
        display: inline-block;
        width: 45%;
}
}
html {
    overflow: -moz-hidden-unscrollable;
    height: 100%;
}
body::-webkit-scrollbar {
    max-width: 100%;
	display: none;
	overflow-x:hidden;
}
.el-tabs__nav-wrap.is-scrollable {
    width: 100%;
}
.el-tabs__active-bar {
    display: none;
}
.el-tabs__nav-wrap {
    width: 100%;
}
.el-tabs__nav-wrap::after {
    display: none;
}

/* .el-tabs__nav {
    float: none;
} */
.el-card__header {
    padding: 0 0 0 0;
    height: 3rem;
    width: auto;
}
.el-card__body {
    padding: 0.5rem;
}


.el-carousel__item h3 {
  color: #475669;
  font-size: 18px;
  opacity: 0.75;
  line-height: 300px;
  margin: 0;
}
.el-carousel__item:nth-child(2n) {
  background-color: #99a9bf;
}
.el-carousel__item:nth-child(2n+1) {
  background-color: #d3dce6;
}

.time {
  font-size: 13px;
  color: #999;
}
.bottom {
  margin-top: 13px;
  line-height: 12px;
}
.button {
  padding: 0;
  float: right;
}
.image {
  width: auto;
  display: block;
}
.clearfix:before,
.clearfix:after {
    display: table;
    content: "";
}
.clearfix:after {
    clear: both
}

#title {
  height:6rem;
}
